:root {
  --accent-color: #0d698b;
  --background-color: #f2f1e8;
  --dark-color: #050533;
  --element-color: #e34234;
}

body {
  background-color: var(--background-color);
  color: var(--accent-color);
  font-family: Roboto, sans-serif;
}

button {
  cursor: pointer;
  font-family: inherit;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.search-form {
  background-color: var(--dark-color);
  height: 70px;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
  box-shadow: 0 8px 14px -2px rgba(0, 0, 0, .67);
}

.search-form input {
  width: 250px;
  height: 40px;
  border-radius: 10px;
  margin-right: 20px;
  padding: 15px;
}

button {
  min-width: 80px;
  border: 3px solid var(--accent-color);
  background-color: var(--element-color);
  border-radius: 10px;
  padding: 5px;
  font-weight: 700;
}

button:hover {
  color: var(--background-color);
  background-color: var(--accent-color);
  border: 3px solid var(--element-color);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-bottom: 30px;
  padding: 0 30px;
  display: flex;
}

.photo-card {
  max-width: 320px;
  border: 2px solid var(--dark-color);
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  display: inline-flex;
  box-shadow: 0 8px 14px -2px rgba(0, 0, 0, .67);
}

.photo-card img {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 10px;
}

.info {
  gap: 15px 40px;
  display: grid;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0 10px;
  display: flex;
}

.likes {
  grid-area: 1 / 1 / 2 / 2;
}

.views {
  grid-area: 1 / 2 / 2 / 3;
}

.comments {
  grid-area: 2 / 1 / 3 / 2;
}

.downloads {
  grid-area: 2 / 2 / 3 / 3;
}

.wrap {
  justify-content: center;
  padding-bottom: 30px;
  display: flex;
}

.wrap button {
  width: 150px;
  box-shadow: 0 8px 14px -7px rgba(0, 0, 0, .67);
}

